import { call, put, fork, all, take } from 'redux-saga/effects';
import { delay } from 'redux-saga/lib';

import * as actions from '../actions';
import apis from '../apis';

function* loading() {
  while (true) {
    const { type } = yield take('*');
    const requestReg = /^START_REQUEST_(.*?)$/;
    const successReg = /^REQUEST_(.*?)_SUCCESS$/;

    if (requestReg.test(type)) {
      const match = requestReg.exec(type);

      yield put({
        type: actions.LOADING,
        payload: {
          namespace: match[1].toLowerCase()
        }
      });
    } else if (successReg.test(type)) {
      const match = successReg.exec(type);

      yield put({
        type: actions.HIDE_LOADING,
        payload: {
          namespace: match[1].toLowerCase()
        }
      });
    }
  }
}

function* search() {
  while (true) {
    const { payload } = yield take(actions.START_REQUEST_SEARCH);
    const { data } = yield call(apis.search, payload.keyword);

    yield call(delay, 500);
    yield put(actions.requestSearchSuccess(data.results, data.total));

    if (data.total === 0) {
      yield put(actions.searchEmpty());
    }
  }
}

function* requestDetail() {
  while (true) {
    const { payload } = yield take(actions.START_REQUEST_DETAIL);
    const { data } = yield call(apis.requestDetail, payload.name);

    yield put(actions.requestDetailSuccess(data));
  }
}

function* rootSaga() {
  yield all([fork(loading), fork(search), fork(requestDetail)]);
}

export default rootSaga;
