import React, { memo } from 'react';

import { BackToTopBtn } from '../../ui';
import { useScroll } from '../../hooks';

const BackToTop = ({ text }) => {
  const { scrollTop, scrollToTop } = useScroll();

  return scrollTop > window.innerHeight ? (
    <BackToTopBtn onClick={scrollToTop}> &uarr; 返回顶部</BackToTopBtn>
  ) : null;
};

export default memo(BackToTop);
