import React, { memo } from 'react';
import { Link } from 'react-router-dom';
import { Tag, Typography } from '../../ui';

const LibraryListItemInfo = ({ name, keywords, description }) => {
  return (
    <div className="library">
      <div>
        <Typography as="div" bold="700" size="18" className="name">
          <Link to={`/libraries/${name}`}>{name}</Link>
        </Typography>
        <Typography color="#999">{description}</Typography>
        {keywords.map(k => (
          <Tag key={k} bgColor="#bdb4b4">
            {k}
          </Tag>
        ))}
      </div>
    </div>
  );
};

export default memo(LibraryListItemInfo);
