import * as actions from '../actions';

const initialState = {
  global: false,
  models: {}
};

const loadingReducer = (state = initialState, { type, payload }) => {
  if (type === actions.LOADING) {
    return {
      ...state,
      global: true,
      models: {
        ...state.models,
        [payload.namespace]: true
      }
    };
  } else if (type === actions.HIDE_LOADING) {
    return {
      ...state,
      global: false,
      models: {
        ...state.models,
        [payload.namespace]: false
      }
    };
  }
  return state;
};

export default loadingReducer;
