import React, { memo } from 'react';

import FilesListItem from './FilesListItem';
import { DetailFilesList } from '../../ui';

const prefix = 'https://cdnjs.cloudflare.com/ajax/libs';

const FilesList = ({ files, version, name, firstVersion }) => {
  return files.length > 0 ? (
    <DetailFilesList>
      {files.map(f => (
        <FilesListItem
          key={f}
          url={`${prefix}/${name}/${version ? version : firstVersion}/${f}`}
        />
      ))}
    </DetailFilesList>
  ) : null;
};

export default memo(FilesList);
