import { useState, useEffect } from 'react';

export const useDocTitle = title => {
  useEffect(
    () => {
      document.title = `${title} - cdnjs.com - The best FOSS CDN for web related libraries to speed up your websites!`;
    },
    [title]
  );
};

const getScrollTop = () => {
  return document.documentElement.scrollTop || document.body.scrollTop;
};

export const useScroll = () => {
  const [scrollTop, setScroll] = useState(0);
  let getScrollTopTimer = 0;
  let scrollToTopTimer = 0;

  const getScroll = () => {
    clearTimeout(getScrollTopTimer);

    getScrollTopTimer = setTimeout(() => {
      const scrTop = getScrollTop();
      setScroll(scrTop);
    }, 100);
  };

  useEffect(() => {
    window.addEventListener('scroll', getScroll);

    return () => {
      window.removeEventListener('scroll', getScroll);

      clearTimeout(getScrollTopTimer);
      clearInterval(scrollToTopTimer);
    };
  }, []);

  const scrollToTop = () => {
    clearInterval(scrollToTopTimer);

    scrollToTopTimer = setInterval(() => {
      const scrTop = getScrollTop();
      const step = Math.ceil(scrTop / 1.5);

      document.documentElement.scrollTop = scrTop - step;
      document.body.scrollTop = scrTop - step;

      if (scrTop === 0) {
        clearInterval(scrollToTopTimer);
      }

      window.onwheel = function() {
        clearInterval(scrollToTopTimer);
      };
    }, 50);
  };

  return { scrollTop, scrollToTop };
};

export const usePullLoad = page => {
  const [currentPage, setPage] = useState(1);
  let timer = 0;

  if (timer) {
    clearTimeout(timer);
  }

  const pullLoad = () => {
    clearTimeout(timer);

    timer = setTimeout(() => {
      const scrollTop = getScrollTop();
      const winHeight = window.innerHeight;
      const docHeight = document.body.offsetHeight;

      if (scrollTop + winHeight >= docHeight) {
        if (currentPage < page) {
          setPage(currentPage + 1);
        }
      }
    }, 200);
  };

  useEffect(
    () => {
      window.addEventListener('scroll', pullLoad);

      return () => window.removeEventListener('scroll', pullLoad);
    },
    [page, currentPage]
  );

  return currentPage;
};
