import React from 'react';
import { connect } from 'react-redux';

import { Container } from '../ui';
import * as actions from '../actions';
import { useDocTitle, usePullLoad } from '../hooks';
import Header from '../components/common/Header';
import IndexLibraryCards from '../components/index/LibraryCards';
import Loading from '../components/common/Loading';
import Search from '../components/index/Search';
import IndexList from '../components/index/SearchResults';

let inputTimer = 0;

const Index = ({ loading, keyword, results, page, empty, total, dispatch }) => {
  const currentPage = usePullLoad(page);

  const changeHandle = value => {
    if (inputTimer) {
      clearTimeout(inputTimer);
    }

    if (value === '') {
      dispatch(actions.resetSearchResults());
    }

    inputTimer = setTimeout(function() {
      if (value) {
        dispatch(actions.startRequestSearch(value));
      }
    }, 1000);

    dispatch(actions.inputSearchKeyword(value));
  };

  const clearKeywords = () => {
    dispatch(actions.inputSearchKeyword(''));
    dispatch(actions.resetSearchResults());
  };

  useDocTitle('cdnjs');

  return (
    <Container style={{ marginTop: keyword === '' ? 160 : 0 }}>
      <Header />
      <Search
        keyword={keyword}
        onChange={changeHandle}
        clearKeywords={clearKeywords}
      />
      {keyword === '' && <IndexLibraryCards />}
      {loading && keyword !== '' && <Loading />}
      {!loading && keyword !== '' && (
        <IndexList
          total={total}
          currentPage={currentPage}
          results={results}
          empty={empty}
        />
      )}
    </Container>
  );
};

const mapStateToProps = state => {
  return {
    keyword: state.search.keyword,
    results: state.search.results,
    total: state.search.total,
    empty: state.search.empty,
    page: state.search.page,
    loading: state.loading.models.search
  };
};

export default connect(mapStateToProps)(Index);
