import React, { Fragment, memo } from 'react';

import { Typography, Tag } from '../../ui';

const LibraryInfo = ({ name, description, keywords, homepage }) => {
  return (
    <Fragment>
      <Typography size="50" color="#333" as="h2" margin="20px 0 0" bold="900">
        {name}
      </Typography>
      {homepage && (
        <Typography
          as="a"
          href={homepage}
          display="inline-block"
          target="_blank"
          color="#f44336"
          size="16"
          margin="16px 0 0 "
        >
          {homepage}
        </Typography>
      )}
      <Typography color="#999" size="14">
        {description}
      </Typography>
      {keywords &&
        keywords.map(k => (
          <Tag key={k} bgColor="#bdb4b4">
            {k}
          </Tag>
        ))}
    </Fragment>
  );
};
export default memo(LibraryInfo);
