import React, { Fragment, memo } from 'react';

import LibraryList from './LibraryList';
import { Typography } from '../../ui';

const SearchResults = ({ total, currentPage, results, empty }) => {
  return (
    <Fragment>
      {total > 0 && (
        <Typography fonSize="14" color="#888">
          共找到{' '}
          <Typography as="span" display="inline" color="#f44336">
            {total}
          </Typography>{' '}
          个结果
        </Typography>
      )}
      {results.length > 0 && (
        <LibraryList libraries={results.slice(0, currentPage * 10)} />
      )}
      {empty && (
        <Typography align="center" color="#999" margin="32px 0 0 ">
          o_O 未找到任何结果
        </Typography>
      )}
    </Fragment>
  );
};

export default memo(SearchResults);
