import * as actions from '../actions';

const initialState = {
  libraries: {},
  empty: false
};

const detailsReducer = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case actions.REQUEST_DETAIL_SUCCESS:
      return {
        ...state,
        empty: false,
        libraries: {
          ...state.libraries,
          [payload.library.name]: payload.library
        }
      };
   
    default:
      return state;
  }
};

export default detailsReducer;
