import chunk from 'lodash/chunk';

import * as actions from '../actions';

const initialState = {
  keyword: '',
  results: [],
  total: 0,
  page: 0,
  byPage: [],
  empty: false
};

const searchReducer = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case actions.INPUT_SEARCH_KEYWORD:
      return {
        ...state,
        keyword: payload.keyword
      };
    case actions.START_REQUEST_SEARCH:
      return {
        ...state,
        empty: false
      };
    case actions.REQUEST_SEARCH_SUCCESS:
      return {
        ...state,
        results: payload.results,
        total: payload.total,
        page: Math.ceil(payload.total / 10),
        byPage: chunk(payload.results, 10)
      };
    case actions.EMPTY_RESULT:
      return {
        ...state,
        empty: true
      };
    case actions.RESET_SEARCH_RESULTS:
      return {
        ...state,
        results: [],
        total: 0,
        page: 0,
        byPage: []
      };
    default:
      return state;
  }
};

export default searchReducer;
