import React, { useState, useEffect, Fragment } from 'react';
import { connect } from 'react-redux';

import * as actions from '../actions';
import { Container, Typography } from '../ui';
import Header from '../components/common/Header';
import FilesList from '../components/detail/FilesList';
import LibraryInfo from '../components/detail/LibraryInfo';
import Loading from '../components/common/Loading';
import { getFiles } from '../utils';
import { useDocTitle } from '../hooks';
import VersionSelector from '../components/detail/VersionSelector';

const LibraryDetail = ({ match, dispatch, loading, libraries }) => {
  const { name } = match.params;
  const library = libraries[name];
  const [version, setVersion] = useState(null);
  const firstFiles = library ? library.assets[0].files : [];
  const files = version ? getFiles(library.assets, version) : firstFiles;

  useDocTitle(name);
  useEffect(
    () => {
      if (!libraries[name]) {
        dispatch(actions.startRequestDetail(name));
      }
    },
    [name]
  );

  return (
    <Container>
      <Header />
      {loading && <Loading />}
      {library && (
        <Fragment>
          <LibraryInfo
            name={library.name}
            keywords={library.keywords}
            description={library.description}
            homepage={library.homepage}
          />
          <VersionSelector
            assets={library.assets}
            onChange={version => {
              setVersion(version);
            }}
          />
          <FilesList
            files={files}
            name={name}
            version={version}
            firstVersion={library.assets[0].version}
          />
        </Fragment>
      )}
    </Container>
  );
};

const mapStateToProps = (state, ownProps) => {
  return {
    libraries: state.detail.libraries,
    loading: state.loading.models.detail
  };
};

export default connect(mapStateToProps)(LibraryDetail);
