import React, { useState, memo } from 'react';

import ActionBtns from '../common/ActionBtns';
import LibraryUrl from '../common/LibraryUrl';
import { FilesItem } from '../../ui';
import { urlType } from '../../utils';

const FilesListItem = ({ url }) => {
  const [visible, toggleVisible] = useState(false);
  const type = urlType(url);

  return (
    <FilesItem
      onMouseOver={() => {
        toggleVisible(true);
      }}
      onMouseLeave={() => {
        toggleVisible(false);
      }}
    >
      <LibraryUrl url={url} />
      {visible && <ActionBtns top={16} type={type} url={url} />}
    </FilesItem>
  );
};

export default memo(FilesListItem);
