import React, { memo } from 'react';

import { VersionSelectWrapper } from '../../ui';

const VersionSelector = ({ assets, onChange }) => {
  return (
    <VersionSelectWrapper name="" id="">
      <span>选择版本</span>
      <select
        name=""
        id=""
        onChange={e => {
          onChange(e.target.value);
        }}
      >
        {assets.map(a => (
          <option key={a.version} value={a.version}>
            {a.version}
          </option>
        ))}
      </select>
    </VersionSelectWrapper>
  );
};

export default memo(VersionSelector);
