/* types */
// loading
export const LOADING = '@@LOADING';
export const HIDE_LOADING = '@@HIDE_LOADING';

// search
export const INPUT_SEARCH_KEYWORD = 'INPUT_SEARCH_KEYWORD';
export const START_REQUEST_SEARCH = 'START_REQUEST_SEARCH';
export const REQUEST_SEARCH_SUCCESS = 'REQUEST_SEARCH_SUCCESS';
export const EMPTY_RESULT = 'EMPTY_RESULT';
export const SEARCH_FAILURE = 'SEARCH_FAILURE';
export const RESET_SEARCH_RESULTS = 'RESET_SEARCH_RESULTS';

// detail
export const START_REQUEST_DETAIL = 'START_REQUEST_DETAIL';
export const REQUEST_DETAIL_SUCCESS = 'REQUEST_DETAIL_SUCCESS';

/* action creator */
const action = (type, payload = {}) => ({
  type,
  payload
});

/* actions */
// search
export const inputSearchKeyword = keyword =>
  action(INPUT_SEARCH_KEYWORD, { keyword });
export const startRequestSearch = keyword =>
  action(START_REQUEST_SEARCH, { keyword });
export const requestSearchSuccess = (results, total) =>
  action(REQUEST_SEARCH_SUCCESS, { results, total });
export const resetSearchResults = () => action(RESET_SEARCH_RESULTS);
export const searchEmpty = () => action(EMPTY_RESULT);

// detail
export const startRequestDetail = name =>
  action(START_REQUEST_DETAIL, { name });
export const requestDetailSuccess = library =>
  action(REQUEST_DETAIL_SUCCESS, { library });
