import React, { memo } from 'react';

import { List, ListItem } from '../../ui';
import LibraryItem from './LibraryListItem';

const LibraryList = ({ libraries }) => {
  return (
    <List>
      <ListItem className="head">
        <div className="library">Library</div>
        <div className="Link">Link</div>
      </ListItem>
      {libraries &&
        libraries.map(library => {
          return <LibraryItem key={library.name} library={library} />;
        })}
    </List>
  );
};

export default memo(LibraryList);
