import axios from 'axios';

const axiosInstance = axios.create({
  baseURL: 'https://api.cdnjs.com/libraries'
});

class Apis {
  search(keyword) {
    return axiosInstance.get('/', {
      params: {
        search: keyword,
        fields: 'version,description,homepage,keywords,repository'
      }
    });
  }
  requestDetail(name) {
    return axiosInstance.get(`/${name}`);
  }
}

export default new Apis();
