import React, { useState, memo } from 'react';

import LibraryListItemInfo from './LibraryListItemInfo';
import ActionBtns from '../common/ActionBtns';
import LibraryUrl from '../common/LibraryUrl';
import { ListItem } from '../../ui';
import { urlType } from '../../utils';

const LibraryListItem = ({ library, showInfo = true }) => {
  const [visible, toggleVisible] = useState(false);
  const { latest } = library;
  const type = urlType(latest);

  return (
    <ListItem
      key={library.name}
      onMouseOver={() => {
        toggleVisible(true);
      }}
      onMouseLeave={() => {
        toggleVisible(false);
      }}
    >
      {showInfo && (
        <LibraryListItemInfo
          name={library.name}
          keywords={library.keywords}
          description={library.description}
        />
      )}
      <div className="link">
        <LibraryUrl url={library.latest} />
        {visible && (
          <ActionBtns
            type={type}
            url={library.latest}
            homepage={library.homepage}
          />
        )}
      </div>
    </ListItem>
  );
};

export default memo(LibraryListItem);
