import React from 'react';
import { Link } from 'react-router-dom';
import { Transition } from 'react-spring';

import { indexLibraries } from '../../utils';
import { IndexCards } from '../../ui';

const IndexLibraryCards = () => {
  return (
    <IndexCards>
      <Transition
        items={indexLibraries}
        keys={item => item.name}
        from={{ transform: 'translate3d(0,90px,0)', opacity: 0 }}
        enter={{ transform: 'translate3d(0,0px,0)', opacity: 1 }}
        leave={{ transform: 'translate3d(0,80px,0)', opacity: 0 }}
        trail={100}
        config={{ mass: 3, tension: 1200, friction: 40 }}
      >
        {item => props => (
          <Link
            to={`/libraries/${item.name}`}
            key={item.name}
            title={item.name}
            style={props}
          >
            <img src={item.logo} alt={item.name} />
          </Link>
        )}
      </Transition>
    </IndexCards>
  );
};

export default IndexLibraryCards;
